import React from 'react'
import styled from 'styled-components'

const Container = styled.div`
    display: flex;
    flex-direction: column;
`

const LogiSection = styled.div`
    display: flex;
    flex-direction: column;
    align-items: center;
    min-height:200px;
    height:calc(100% - 560px);

    img{
        width:272px;
        height:92px;
        margin-top: auto;
    }
`
const SearchSection = styled.div`
    padding: 20px;
    display: flex;
    justify-content: center;
    flex-direction: column;
`
const Form = styled.form``
const Search = styled.div`
    display: flex;
    align-items: center;
    padding:0px 14px;
    background: #fff;
    border: 1px solid #dfe1e5;
    box-shadow: none;
    border-radius:24px;
    z-index:3;
    height: 44px;
    margin: 0 auto;
    max-width: 584px;

    &:hover {
        background-color: #fff;
        box-shadow: 0 1px 6px rgb(32 33 36 / 28%);
        border-color: rgba(223,225,229,0);
    }
`
const SearchIcon = styled.div`
    color: #9aa0a6;
    height: 30px;
    width: 30px;
    padding-right:10px;
    display: flex;
    align-items: center;

    & svg {
        fill=#9aa0a6;
    }
`
const SearchInput = styled.input`
    display: flex;
    flex:1;
    height: 40px;
    background:transparent;
    border:0;
    margin:0;
    padding:0;
    color:rga(0, 0, 0, 0.87);
    word-wrap: break-word;
    outline: none;
    height:34px;
    font-size:16px;    
`
const Micicon = styled.div`
    display: flex;
    cursor:pointer;
    align-items: center;
    border:0;
    background:transparent;
    outline:none;
    padding:0 8px;
    width: 40px;
    line-height: 44px;
    height: 44px;

    svg{
        height: 24px;
        width: 24px;
        vertical-align:middle;
    }
`
const ButtonSection = styled.div`
    display: flex;
    justify-content: center;
    height: 70px;
    padding-top: 10px;
    top:53px;
    x-index:0;

    button {
        background-color:#f8f9fa;
        border: 1px solid #f8f9fa;
        border-radius: 4px;
        color:#3c4043;
        font-family: arial, sans-serif;
        font-size:14px;
        margin: 11px 4px;
        padding: 0 16px;
        line-height: 27px;
        height: 36px;
        min-width: 54px;
        text-align:center;
        cursor:pointer;
        user-select: none;

        &:hover {            
            box-shadow: 0 1px 1px rgb(0 0 0 / 5%);
            background-color: #f8f9fa;
            border: 1px solid #dadce0;
            color: #202124;
        }
    }
`

const Home = () => {
    return (
        <Container>
            <LogiSection>
                <img
                    src="https://www.google.com/images/branding/googlelogo/2x/googlelogo_color_272x92dp.png"
                    alt="Googlelogo"
                />
            </LogiSection>
            <SearchSection>
                <Form action="https://www.google.com/search?q=" method="GET" role="search">
                    <Search>
                        <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" className="bi bi-search" viewBox="0 0 16 16">
                            <path d="M11.742 10.344a6.5 6.5 0 1 0-1.397 1.398h-.001c.03.04.062.078.098.115l3.85 3.85a1 1 0 0 0 1.415-1.414l-3.85-3.85a1.007 1.007 0 0 0-.115-.1zM12 6.5a5.5 5.5 0 1 1-11 0 5.5 5.5 0 0 1 11 0z" />
                        </svg>
                        <SearchIcon />
                        <SearchInput type="text" title="Search"/>
                        <Micicon title="Search by voice">
                            <svg className="goxjub" focusable="false" viewBox="0 0 24 24" xmlns="http://www.w3.org/2000/svg"><path fill="#4285f4" d="m12 15c1.66 0 3-1.31 3-2.97v-7.02c0-1.66-1.34-3.01-3-3.01s-3 1.34-3 3.01v7.02c0 1.66 1.34 2.97 3 2.97z"></path><path fill="#34a853" d="m11 18.08h2v3.92h-2z"></path><path fill="#fbbc05" d="m7.05 16.87c-1.27-1.33-2.05-2.83-2.05-4.87h2c0 1.45 0.56 2.42 1.47 3.38v0.32l-1.15 1.18z"></path><path fill="#ea4335" d="m12 16.93a4.97 5.25 0 0 1 -3.54 -1.55l-1.41 1.49c1.26 1.34 3.02 2.13 4.95 2.13 3.87 0 6.99-2.92 6.99-7h-1.99c0 2.92-2.24 4.93-5 4.93z"></path></svg>
                        </Micicon>
                    </Search>
                </Form>
                <ButtonSection>
                    <button>Google Search</button>
                    <button>I'm filling Lucky</button>
                </ButtonSection>
            </SearchSection>
        </Container>
    )
}

export default Home
