import Home from './components/Home'
import Navbar from './components/Navbar'
import About from './pages/About'
import Store from './pages/Store'
import Gmail from './pages/Gmail'
import Images from './pages/Images'
import {Route, Switch } from 'react-router-dom'
import { GlobalStyle } from './GlobalStyles'


function App() {
  return (
    <>
    <GlobalStyle />
      <Navbar />
      <Switch>
        <Route path="/google-homepage" exact component={Home} />
        <Route path="/google-homepage/about" exact component={About} />
        <Route path="/google-homepage/store" exact component={Store} />
        <Route path="/google-homepage/gmail" exact component={Gmail} />
        <Route path="/google-homepage/images" exact component={Images} />
      </Switch>
    </>
  );
}

export default App;
