import React from 'react'
import appIcon from '../images/apps.svg'
import Profile from '../images/download.png'
import styled from 'styled-components'
import { Link } from 'react-router-dom'

const Nav = styled.nav`
    display:flex;
    justify-content:space-between;
    align-items:center;
    height:60px;
    font-size:14px;
`

const Leftlinks = styled.div`
    padding: 10px;
    margin-left:10px;
`
const Rightlinks = styled.div`
    padding: 10px;
    margin-right:16px;
    display:flex;
    align-items:center;

    img{
        height:30px;
        width:30px;
        border-radius:50%;
    }
`
const NavLinks = styled(Link)`
    padding: 0 8px;
    text-decoration:none;
    color: #000;

    img{
        height:40px;
        width:40px;
        padding: 8px;
        opacity:0.7;

        &:hover { background:#eee;}
    }
`

const Navbar = () => {
    return (
        <Nav>
            <Leftlinks>
                <NavLinks to="/google-homepage/about">About</NavLinks>
                <NavLinks to="/google-homepage/store">Store</NavLinks>
            </Leftlinks>
            <Rightlinks>
                <NavLinks to="/google-homepage/gmail">Gmail</NavLinks>
                <NavLinks to="/google-homepage/images">Images</NavLinks>
                <NavLinks to="/google-homepage" title="Google Apps">
                    <img src={appIcon} alt="appIcon" />
                </NavLinks>
                <Link to="/google-homepage">
                    <img src={Profile} alt="Profile" />
                </Link>
            </Rightlinks>
        </Nav>
    )
}

export default Navbar
